import React, {useCallback,useState} from 'react';
import './Cart.css';
import data from './data';

const Cart = ({Elemdata,Tot,setCarts}) => {
    const [inc,setInc] = useState(1);
    const [dec,setDec] = useState(1);
    
    const addItem = useCallback(() => {
        Elemdata['quantity'] += 1;
        data[Elemdata['id']-1]['quantity'] += 1; 
        setInc(inc+1);
        Tot();
    })
    const subItem = useCallback(() => {
        if(Elemdata['quantity'] > 1){
            Elemdata['quantity'] -= 1;
            data[Elemdata['id']-1]['quantity'] -= 1;
            setDec(dec-1);
            Tot();
        }else if (Elemdata['quantity'] === 1){
            Elemdata['quantity'] -= 1;
            data[Elemdata['id']-1]['quantity'] -= 1;
            setCarts((oldItem) => [...oldItem.pop(Elemdata),...oldItem]);
            setDec(dec-1);
            Tot();
        }
    })
    return (
        
            <div className='item'>
                <div><img id='img' src={require(Elemdata['imgsrc'])}></img></div>
                <div>
                    <div id='name'>{Elemdata['name']}</div>
                    <div id='price'>
                        <span id='oprice'>{Elemdata['oprice']} </span>
                        <span id='dprice'>${Elemdata['dprice']}</span>
                        <span id='discount'>{Elemdata['discount']} off</span>
                    </div>
                    <div id='quant'>
                        <button onClick={subItem}><img src={require('./minus.png')} width='12px'/></button>
                        <div>{Elemdata['quantity']}</div>
                        <button onClick={addItem}><img src={require('./plus.png')} width='13px'/></button>
                    </div>
                </div>
            </div>
    )
}

export default Cart;


